import { OpdStatusComponent } from './opd-status.component';
import { Routes, RouterModule, Router } from '@angular/router';
import { NgModule } from '@angular/core';

const routes: Routes = [
  { path: '', component: OpdStatusComponent, data: { title: 'Patient Status' } }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
  declarations: []
})
export class OpdStatusRouterModule {}
