import { Component, OnInit, OnDestroy } from '@angular/core';
import { LoginApiService } from '@core/services';
import { UserService } from '@core/services/user.service';
import { UserSession } from '@core/models/user-session';
import { ToastrService } from 'ngx-toastr';
import { OpdApiService, EmrApiService } from '@core/services';

@Component({
  templateUrl: './opd-status.component.html',
  styleUrls: ['./opd-status.component.scss']
})
export class OpdStatusComponent implements OnInit {
  private sessionData: UserSession;
  public specialtyList$: any;
  doctorsList$: any;
  tabsList: any;
  specialityName: any;
  active: string;
  doctorObj: any;
  patientStatus: any[] = [];

  constructor(
    userService: UserService,
    private opdApiService: OpdApiService,
    private emrApiService: EmrApiService,
    private toaster: ToastrService,
    private loginApiService: LoginApiService
  ) {
    this.sessionData = userService.getUser();
  }

  ngOnInit() {
    this.active = 'true';
    this.fetchListOfSpecialities();
  }

  onSelectSpeciality(selectedValue) {
    this.doctorObj = null;
    this.fetchOpdPatientCurrentStatus(selectedValue, '');
    this.fetchDoctorsBasedOnSpecialityIdEmr(selectedValue);
    this.fetchOrgCustomTabsBasedOnSpeciality(selectedValue);
  }

  onSelectDoctor(selectedValue: string, doctorValue: string) {
    this.fetchOpdPatientCurrentStatus(selectedValue, doctorValue);
  }

  onSelectFilter(selectedValue: string, doctorValue: string) {
    this.fetchOpdPatientCurrentStatus(selectedValue, doctorValue);
  }

  fetchListOfSpecialities() {
    const payload = {
      orgId: this.sessionData.orgId,
      regId: this.sessionData.regId
    };
    this.loginApiService.fetchListOfSpecialities(payload).subscribe(
      Response => {
        if (Response.responseCode === 'E200') {
          this.specialtyList$ = Response.data;
          this.specialityName = this.specialtyList$[0];
          this.fetchOpdPatientCurrentStatus(this.specialtyList$[0], '');
        } else if (Response.responseCode === 'E400') {
          this.toaster.error('failed', Response.validations[0].message +
          ' ' +
          Response.validations[0].errorCode);
        }
      },
      err => {
        console.log(err);
        this.toaster.error(
          'Failure!',
          err.error.error +
            ' ' +
            err.error.status
        );
      }
    );
  }
  fetchOpdPatientCurrentStatus(selectedValue, doctorValue) {
    const payload = {
      orgId: this.sessionData.orgId,
      orgGrpId: this.sessionData.orgGroupId,
      createdbyUserId: this.sessionData.regId,
      specialityId: selectedValue ? selectedValue.specialityId : ' ',
      doctorId: doctorValue ? doctorValue.empId : null,
      active: this.active ? this.active : false,
    };
    this.opdApiService.fetchOpdPatientCurrentStatus(payload).subscribe(
      (Response: any)  => {
        if (Response.responseCode === 'E200') {
          this.patientStatus = Response.data;
        } else if (Response.responseCode === 'E400') {
          this.toaster.error(
            'failed',
            Response.validations[0].message +
              ' ' +
              Response.validations[0].errCode
          );
        }
      },
      err => {
        this.toaster.error(
          'Failure!',
          err.data.validations[0].message +
            ' ' +
            err.data.validations[0].errCode
        );
      }
    );
  }
  fetchDoctorsBasedOnSpecialityIdEmr(selectedValue) {
    const payload = {
      orgId: this.sessionData.orgId,
      docName: '',
      specalityList: []
    };
    payload.specalityList.push(selectedValue.specialityId);
    this.emrApiService.fetchDoctorsBasedOnSpecialityIdEmr(payload).subscribe(
      (Response: any)  => {
        if (Response.responseCode === 'E200') {
          this.doctorsList$ = Response.data;
        } else if (Response.responseCode === 'E400') {
          this.toaster.error(
            'failed',
            Response.validations[0].message +
              ' ' +
              Response.validations[0].errCode
          );
        }
      },
      err => {
        this.toaster.error(
          'Failure!',
          err.data.validations[0].message +
            ' ' +
            err.data.validations[0].errCode
        );
      }
    );
  }

  fetchOrgCustomTabsBasedOnSpeciality(selectedValue) {
    const payload = {
      orgId: this.sessionData.orgId,
      authenticatedUserId: this.sessionData.regId,
      specialityId: selectedValue ? selectedValue.specialityId : ''
    };
    this.emrApiService.fetchOrgCustomTabsBasedOnSpeciality(payload).subscribe(
      (Response: any)  => {
        if (Response.responseCode === 'E200') {
          this.tabsList = Response.data;
        } else if (Response.responseCode === 'E400') {
          this.toaster.error(
            'failed',
            Response.validations[0].message +
              ' ' +
              Response.validations[0].errCode
          );
        }
      },
      err => {
        this.toaster.error(
          'Failure!',
          err.data.validations[0].message +
            ' ' +
            err.data.validations[0].errCode
        );
      }
    );
  }
}
